*, :before, :after {
  box-sizing: border-box;
}

body {
  color: #2a2a2a;
  font-family: Open Sans, -apple-system, BlinkMacSystemFont, Segoe UI, Roboto, Oxygen, Ubuntu, Cantarell, sans-serif;
  line-height: 1.5;
}

img {
  max-width: 100%;
  height: auto;
  display: block;
}

.search-form {
  width: 100%;
  max-height: 50px;
  background-color: rgba(0, 0, 255, .525);
  justify-content: center;
  padding: 8px;
  display: flex;
  position: fixed;
  top: 0;
  left: 0;
}

.button:hover, .button:focus {
  cursor: pointer;
  background-color: rgba(255, 99, 71, .36);
}

.button.load-more {
  margin-bottom: 100px;
  margin-left: auto;
  margin-right: auto;
  display: block;
}

.button.is-hidden {
  display: none;
}

.js-articles-container {
  grid-gap: 20px;
  max-width: calc(100vw - 80px);
  grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
  margin: 40px auto 30px;
  padding: 0;
  list-style: none;
  display: grid;
}

.info {
  justify-content: center;
  display: flex;
}

.js-articles-container img {
  width: 100%;
  height: 200px;
  object-fit: cover;
  display: block;
}

.info p {
  color: rgba(42, 42, 42, .737);
  margin: 10px 5px;
  font-size: small;
  text-decoration: none;
}

